.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
    margin: 0px;
    padding: 0px;
    font-family: "Fira Sans", sans-serif;
}

.alertBox {
    width: 400px;
    border: 1px solid black;
    padding: 20px;
    border-radius: 5px;
    background-color: #4b7499;
    color: #000;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 10px;
    /* position: absolute; */
    /* top:150px; */
    /* left: calc(50vw -200px); */
    /* z-index: 5; */
}

.alertBody {
    background: #003666;
    width: 80%;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    border: none;
}

.alertBody:focus {
    outline: none;
}

.alertOk {
    background-color: #003666;
    color: white;
    width: 50%;
    height: 40px;
    border-radius: 5px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    margin: 10px;
}

.alertOk:hover {
    background-color: rgb(0, 111, 221);
}

.fillAlert {
    color: white;
    width: 30%;
    height: 40px;
    border-radius: 5px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    margin: 10px;
}



.cover {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 5;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.424);
}

.fillAlert{
	background-color: #1ab552;
	color: white;
	border: none;
}

.fillAlert:hover{
	background-color: rgb(28,202,90);
}

.cancelAlert{
	border:2px solid #1ab552;
    line-height: 36px;
	color: white;
	background-color: transparent;
}

.cancelAlert:hover{
	background-color: #1ab552;
	border:2px solid #1ab552;
}


@media screen and (max-width: 450px) {
    .alertBox {
        width: 300px;
    }
    .alertBody {
        width: 90%;
    }
}

@media screen and (max-width: 350px) {
    .alertBox {
        width: 250px;
    }
    .alertBody {
        width: 90%;
    }
}

